import { ApplicationBasedRenderer, } from 'src/renderer/renderer';
export class ImageDataRenderer extends ApplicationBasedRenderer {
    static get type() { return 'image-data'; }
    async _render(ocx, config, options) {
        const style = options?.style;
        const parent = ocx.create_child({
            attrs: {
                'data-source-media-type': this.media_type,
            },
        });
        const canvas = ocx.CLASS.create_element({
            parent,
            tag: 'canvas',
            style,
        });
        const ctx = canvas.getContext('2d');
        if (ctx) {
            const iter_config = Array.isArray(config) ? config : [config];
            for (const { x = 0, y = 0, image_data } of iter_config) {
                ctx.putImageData(image_data, x, y);
            }
        }
        return parent;
    }
}
