import { ApplicationBasedRenderer, } from 'src/renderer/renderer';
import { load_Plotly, } from './plotly';
export class PlotlyRenderer extends ApplicationBasedRenderer {
    static get type() { return 'plotly'; }
    async _render(ocx, plotly_config, options) {
        if (typeof plotly_config !== 'object') {
            throw new Error('plotly_config must be an object');
        }
        const style = options?.style;
        let data;
        let layout;
        let config;
        let frames;
        if ('data' in plotly_config && Array.isArray(plotly_config.data)) {
            ({
                data,
                layout = {},
                config = {},
                frames = [],
            } = plotly_config);
        }
        else {
            data = plotly_config;
            layout = {};
            config = {};
            frames = [];
        }
        layout ??= {};
        layout.plot_bgcolor ??= 'rgba(0, 0, 0, 0)';
        layout.paper_bgcolor ??= 'rgba(0, 0, 0, 0)';
        config.displayModeBar = false; // remove icons/links from top-right of plot
        const parent = ocx.create_child({
            attrs: {
                'data-source-media-type': this.media_type,
            },
        });
        const output_element = ocx.CLASS.create_element({
            parent,
            style,
        });
        const Plotly = await load_Plotly();
        await Plotly.newPlot(output_element, { data, layout, config, frames }); // render to the output_element
        return parent;
    }
}
